.burger {
  display: none;
}

@media (width <= 500px) {
  .burger {
    z-index: 50;
    justify-content: flex-end;
    align-items: center;
    width: 30px;
    height: 23px;
    display: flex;
    position: relative;
  }

  .burger span {
    background-color: #fff;
    width: 100%;
    height: 2.5px;
    transform: scale(1);
  }

  .burger:before, .burger:after {
    content: "";
    background-color: #fff;
    width: 100%;
    height: 2.5px;
    transition: all .3s;
    position: absolute;
  }

  .burger:before {
    top: 0;
  }

  .burger:after {
    bottom: 0;
  }

  .burger.active span {
    transform: scale(0);
  }

  .burger.active:before {
    top: 50%;
    transform: rotate(-45deg)translate(0, -50%);
  }

  .burger.active:after {
    bottom: 50%;
    transform: rotate(45deg)translate(0, 50%);
  }
}

